export default {
    name:'center',
    // 获取登录名
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        // 跳转到用户界面  使用编程式路由跳转
        // this.$router.push  这个方法会向history添加一个记录  点击后退返回上一个界面
        // this.$router.replace不会向history添加记录 返回上上个页面  上一个页面的记录不存在
        this.$router.replace('/user/'+this.loginname)
    }
}



